<template>
  <el-container class="home_container">
    <!-- 这是头部区 -->
    <el-header>
      <div class="header_img_span">
        <img src="../assets/heima.png" />
        <span class="biao_ti">电商后台管理系统</span>
      </div>
      <el-button type="info" @click="tuichu">退出</el-button>
    </el-header>
    <!-- 主题区 -->
    <el-container>
      <!-- 右边导航区 -->
      <el-aside :width="collShow ? '64px':'200px'">
        <!-- ====折叠菜单 -->
        <div class="toggle_button" @click="toggleBtn">|||</div>
        <!-- 侧边栏菜单区 -->
        <el-menu background-color="#333744" text-color="#fff" :collapse='collShow'
         unique-opened active-text-color="#409EFF" 
         :collapse-transition="false" :router="true" :default-active="active">
          <!-- ============一级导航菜单 ============-->

          <el-submenu :index="item.id + ''" v-for="(item) in menulist" :key="item.id">
            <!-- 一级菜单的模板区 -->
            <template slot="title">
              <!-- 图标 -->
              <i :class="iconObj[item.id]"></i>
              <!-- 文本 -->
              <span>{{item.authName}}</span>
            </template>


            <!-- ===========二级菜单 =================-->
              <el-menu-item :index="'/' + subItem.path" v-for="(subItem) in item.children"
               :key="subItem.id" @click="saveNav('/' + subItem.path)">
                 <template slot="title">
              <!-- 图标 -->
              <i class="el-icon-menu"></i>
              <!-- 文本 -->
              <span>{{subItem.authName}}</span>
            </template>
              </el-menu-item>
           
          </el-submenu>
          
        </el-menu>
      </el-aside>

      <!-- ====左边区==== -->
      <el-main>
        <!-- 路由站位符 -->
         <router-view/>
      </el-main>
    </el-container>
  </el-container>
</template>

<script>
export default {
  data(){
    return{
      // 左侧菜单数据
      menulist:[],
      // icon
      iconObj:{
        '125':'iconfont icon-users',
        '103':'iconfont icon-tijikongjian',
        '101':'iconfont icon-shangpin',
        '102':'iconfont icon-danju',
        '145':'iconfont icon-baobiao'
      },
      //是否折叠
      collShow:false,
      // 被激活的链接地址
      active:''
    }
  },
  name: "Home",
  components: {},
  created(){
    this.getMenuList()
    this.active = window.sessionStorage.getItem('active')
  },
  methods: {
    tuichu() {
      window.sessionStorage.clear();
      this.$router.push("/log");
    },
    // 获取所以菜单
   async getMenuList(){
      const {data:res} = await this.$http.get('menus')
      console.log(res)
      if(res.meta.status !== 200) return this.$message.error(res.meta.msg)
      this.menulist = res.data
    },
    // 切换菜单的折叠与站开启
    toggleBtn(){
        this.collShow = ! this.collShow
    },
    // 保存链接的激活状态
    saveNav(active){
      window.sessionStorage.setItem('active',active)
      this.active = active
    }
  },
};
</script>
<style  scoped>
.home_container {
  height: 100%;
}
.el-header {
  background-color: #373d41;
  display: flex;
  justify-content: space-between;
  padding-left: 0px;
  align-items: center;
  color: white;
  font-size: 25px;
}
.el-aside {
  background-color: #333744;
}
.el-menu{
  border: 0px solid;
}
.el-main {
  background-color: #eaedf1;
}
.header_img_span {
  display: flex;
  justify-content: center;
  align-items: center;
}
.biao_ti {
  margin-left: 20px;
}
.iconfont{
  font-size:20px;
  margin-right: 10px;
}
.toggle_button{
  background-color: #4A5064;
  font-size: 10px;
  color: white;
  line-height: 24px;
  text-align: center;
  letter-spacing: 0.4em;
  cursor: pointer;
}
</style>
