<template>
  <div class="log_da">
    <div class="log_count_da">
      <!-- 头像区 -->
      <div class="log_image">
        <img src="../assets/logo.png" />
      </div>

      <!-- 登录用户名区 -->
      <el-form label-width="0px" ref="logRef" :rules="rules" :model="from" class="log_denglu">
        <el-form-item prop="username">
          <el-input v-model="from.username" prefix-icon="iconfont icon-user"></el-input>
        </el-form-item>
        <!-- 密码区 -->
        <el-form-item prop="password">
          <el-input v-model="from.password" type="password" prefix-icon="iconfont icon-3702mima"></el-input>
        </el-form-item>
        <!-- 按钮区 -->
        <el-form-item class="log_deng_chong">
          <el-button type="primary" @click="denglu">登录</el-button>
          <el-button type="info" @click="chongzhi">重置</el-button>
        </el-form-item>
      </el-form>
    </div>
    <div></div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      // 这是一个登录表单对象
      from: {
        username: "admin",
        password: "123456",
      },
      // 表单验证对像
      rules: {
        // 验证用户名
        username: [
          { required: true, message: "请输入登录名称", trigger: "blur" },
          {
            min: 3,
            max: 10,
            message: "长度在 3 到 10 个字符",
            trigger: "blur",
          },
        ],
        // 验证密码
        password: [
          { required: true, message: "请输入登录密码", trigger: "blur" },
          {
            min: 5,
            max: 15,
            message: "长度在 6 到 15 个字符",
            trigger: "blur",
          },
        ],
      },
    };
  },
  methods: {
    // 重置按钮
    chongzhi() {
      // console.log(this)
      this.$refs.logRef.resetFields();
    },
    denglu() {
      this.$refs.logRef.validate(async (valid) => {
        // console.log(valid);
        if (!valid) return;
        const { data: res } = await this.$http.post("login", this.from);
        // console.log(res);
        if (res.meta.status !== 200) return this.$message.error("登录失败！");
        this.$message.success("登录成功！");
        window.sessionStorage.setItem("token",res.data.token)
        this.$router.push('/home')
      });
    },
  },
};
</script>

<style scoped>
.log_da {
  width: 100%;
  height: 100%;
  background-color: #2b4b6b;
}
.log_count_da {
  position: fixed;
  top: 220px;
  left: 500px;
  width: 450px;
  height: 300px;
  background-color: white;
  border-radius: 10px;
}
.log_image {
  width: 140px;
  height: 140px;
  background-color: white;
  position: fixed;
  border-radius: 50px;
  top: 150px;
  left: 660px;
}
.log_image img {
  border-radius: 50px;
  width: 100%;
  height: 100%;
}
.log_deng_chong {
  display: flex;
  justify-content: flex-end;
}
.log_denglu {
  position: absolute;
  bottom: 0px;
  width: 100%;
  padding: 0 20px;
  box-sizing: border-box;
}
</style>